import AppSlider from "./AppSlider";

(function ($) {
  class SliderPlugin {
    constructor(options) {
      this.appSlider = new AppSlider(options);
    }

  }

  $.fn.sliderPlugin = function (options) {
    options = $.extend({
      target: this,
      isHorizontal: true,
      isRangeSlider: true,
      isProgressBar: true,
      max: 100,
      min: 0,
      step: 1,
      isFlag: true,
      isScale: true,
      scaleQuantity: 6
    }, options);
    return this.each(function () {
      if (!$.data(this, "sliderPlugin")) {
        $.data(this, "sliderPlugin", new SliderPlugin(options));
      }
    });
  };
})(jQuery);

let plug = $.fn.sliderPlugin;
export default plug;